@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,500);
.uploadArea {
  width: 40%;
  height: 100%;
  border-radius: 4px;
  background-color: red;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-flow: column;
  cursor: pointer;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative; }
  .uploadArea .loader-container {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.5); }
    .uploadArea .loader-container .loader,
    .uploadArea .loader-container .loader:before,
    .uploadArea .loader-container .loader:after {
      background: #ffffff;
      -webkit-animation: load1 1s infinite ease-in-out;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em; }
    .uploadArea .loader-container .loader {
      color: #ffffff;
      text-indent: -9999em;
      margin: 88px auto;
      position: relative;
      font-size: 11px;
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-animation-delay: -0.16s;
      animation-delay: -0.16s; }
    .uploadArea .loader-container .loader:before,
    .uploadArea .loader-container .loader:after {
      position: absolute;
      top: 0;
      content: ''; }
    .uploadArea .loader-container .loader:before {
      left: -1.5em;
      -webkit-animation-delay: -0.32s;
      animation-delay: -0.32s; }
    .uploadArea .loader-container .loader:after {
      left: 1.5em; }

@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em; }
  40% {
    box-shadow: 0 -2em;
    height: 5em; } }

@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em; }
  40% {
    box-shadow: 0 -2em;
    height: 5em; } }
  .uploadArea__icon .arrow-upload {
    color: white;
    font-size: 100px; }
  .uploadArea__title {
    height: 50px;
    width: 60%;
    text-align: center; }
  @media screen and (max-width: 768px) {
    .uploadArea {
      height: 40%;
      width: 100%; } }

